label {
  display: inline-block;
  margin-bottom: 0;
  color: #3e3e3e;
  font-size: 13px !important;
  font-family: "GothamNarrowBold", sans-serif !important;
  min-width: 120px;
  max-width: 120px;
  word-break: break-word;
}

@media screen and (max-width: 767px) {
  .nobtm-pad .passtext {
    margin-right: 0;
    margin-top: 6px;
    margin-bottom: 25px;
  }

  .nobtm-pad button {
    margin: 0 auto;
  }

  .email-block.col {
    min-width: 100%;
  }

  .mobileText {
    display: block !important;
    background: white;
    text-align: center;
    padding: 10px 0;
    width: 115%;
    position: relative;
    left: -30px;
    top: -15px;
    border-top: 2px solid #d1d4d8;
    border-bottom: 2px solid #d1d4d8;
  }

  .mobileText p {
    font-weight: bold;
  }

  .email-block label {
    padding-top: 25px;
  }

  .cursor-rem {
    display: none;
  }
}

.mobileText {
  display: none;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .email-block.col {
    min-width: 100%;
  }

  .nobtm-pad .passtext {
    width: 50% !important;
  }
}

@media screen and (min-width: 320px) and (max-width: 359px) {
  .mobileText {
    left: -22px;
  }
}